.about {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0 50px;
  padding-bottom: 100px;

  @media (max-width: $vp-768) {
    padding: 0 25px;
    padding-bottom: 80px;
  }
}

.about__img {
  background-size: contain;

  @media (max-width: $vp-768) {
    display: none;
  }
}

.about__wrapper {
  margin-left: 100px;
  max-width: 970px;
  width: 100%;

  @media (max-width: $vp-1024) {
    margin-left: 25px;
    width: 100%;
  }

  @media (max-width: $vp-768) {
    width: 100%;
    margin: 0;
  }
}

.about__title {
  @include fonts(36px, 51px);
  margin: 5px 0 15px 0;
}

.about__text {
  margin: 0;
  margin-bottom: 20px;
}

.about__text--hidden {
  display: none;
}

.about__button {
  @include fonts(14px, 16px);
  font-weight: 700;
  margin-top: 20px;
  color: #00b2ff;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  width: 180px;
  padding: 14px 0;
  cursor: pointer;
}
