.header {
  position: fixed;
  background: linear-gradient(180deg, #041827 84.9%, #04182700 100%);
  width: 100%;
  padding: 0 40px;
  z-index: 1;

  @media (max-width: $vp-1024) {
    position: relative;
    padding: 0 50px;
  }

  @media (max-width: $vp-768) {
    padding: 0 25px;
  }
}

.header__list {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 0 35px 0;
  margin: 0;

  @media (max-width: $vp-1024) {
    justify-content: space-between;
    padding: 20px 0 25px 0;
  }
}

.header__item {
  &:not(:last-child) {
    margin-right: 150px;

    @media (max-width: $vp-1220) {
      margin-right: auto;
    }

    @media (max-width: $vp-1024) {
      margin: 0;
    }
  }
}

.header__item--grid {
  display: grid;
  grid-template-columns: repeat(2, 225px);
  gap: 20px 30px;

  @media (max-width: $vp-1024) {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
}

.header__item-hidden {
  @media (max-width: $vp-1024) {
    display: none;
  }
}

.header__link {
  color: #f2f5f7;
  font-size: 16px;
  line-height: 18px;
  padding: 5px;
}

.header__link-icon {
  margin-right: 15px;

  @media (max-width: $vp-1024) {
    margin: 0;
  }
}

.header__link-text {
  @media (max-width: $vp-1024) {
    display: none;
  }
}

.header__button {
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  color: #f2f5f7;
  background: #04182700;
  padding: 10px 15px;
  cursor: pointer;
}
