  line-height: 16px;
  word-spacing: 10px;
  opacity: 0.8;
}

.form__control-mark {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
